.service__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.service__item {
  margin-bottom: 30px;
}

.service__wrapper {
  display: grid;
  grid-template-columns: 50% 25% 25%;
  box-shadow: 0px 5px 20px 1px rgba(188, 190, 191, 0.6);
  border-radius: 15px;
  padding: 30px 40px;
  background-color: $white;
}

.service__title {
  grid-column: 1 / 2;
  font-family: $font-bold;
  margin: 0;
}

.service__intro {
  grid-column: 1 / 2;
  font-family: $font-italic;
  line-height: 25px;
  margin-bottom: 0;
}

.service__price {
  grid-column: 2 / 3;
  grid-row: 1 / 3;
  align-self: center;
  text-align: center;
  font-family: $font-bold;
  font-size: 30px;
}

.service__buttons {
  grid-column: 3 / 4;
  grid-row: 1 / 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.service__button {
  width: 170px;
  text-align: center;
  text-transform: uppercase;
  font-family: $font-bold;
  background-color: $text-hover;
  color: $white;
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 40px;
  border: 1px solid $text-hover;
  transition: opacity 0.5s ease;

  &--more {
    margin-top: 20px;
  }

  &:hover {
    background-color: $white;
    color: $text-hover;
  }

  &:active {
    background-color: $white;
    color: $text-hover;
  }

  &:focus {
    background-color: $white;
    color: $text-hover;
  }
}

.description_show {
  display: none;
}

.service__description {
  grid-column: 1 / 4;
  grid-row: 3 / 4;
}