.about {
  margin-top: 40px;
  background-color: $white;
  box-shadow: 0px 5px 20px 1px rgba(188, 190, 191, 0.6);
  border-radius: 15px;
  padding: 30px 60px 60px;
  margin-bottom: 30px;
}

.about__title {
  font-family: $font-bold;
}

.about__title-add {
  font-family: $font-bold;
}

.about__link {
  text-decoration: none;
  color: $text;

  &:hover {
    color: $text-hover;
  }

  &:active {
    color: $text-hover;
  }

  &:focus {
    color: $text-hover;
  }
}